<template>
  <app-layout>
    <template v-slot:header>
      <catalog-search-bar />
    </template>

    <template v-slot:menu>
      <catalog-side-menu />
    </template>

    <template v-slot:content>
      <div class="catalog">
        <div v-if="loading">
          <progress class="progress is-large is-info" max="100">60%</progress>
        </div>

        <div v-if="products">
          <div v-if="products.length > 0">
            <div
              v-bind:class="
                'products ' +
                (mq.tablet || mq.other ? 'products-tablet' : 'products-phone')
              "
            >
              <card-image
                v-for="product in products"
                :key="product.id"

                :product="product"
                :favorite="isFavorite(product.id)"
              />
            </div>
          </div>

          <div v-else>
            <p style="text-align: center">Aucun rsultat trouv...</p>
          </div>
        </div>
      </div>
    </template>
  </app-layout>
</template>


<script setup>
import { useMq } from "vue3-mq"

const mq = useMq()
</script>


<script>
import AppLayout from "./layout/AppLayout.vue"
import CatalogSearchBar from "./CatalogSearchBar.vue"
import CatalogSideMenu from "./catalog/CatalogSideMenu.vue"
import CardImage from "./CardImage.vue"
import { mapState } from "vuex"
import { useObservable } from "@vueuse/rxjs"
import { liveQuery } from "dexie"
import { toLocalizedProduct } from "../types/Product"
import { fromLocalDatabaseProductEntries } from "../types/LocalDatabaseProductEntry"


export default {
  inject: ["mq"],
  name: "Catalog",

  components: {
    AppLayout,
    CatalogSearchBar,
    CatalogSideMenu,
    CardImage,
  },

  data() {
    return {
      products: [],
    }
  },

  computed: mapState({
    db: state => state.db,
    loading: state => state.catalog.loading,
    filters: state => state.catalog.filters,
  }),

  methods: {
    getPackages(product) {
      var data = [];
      for (var i = 0; i < product.packages.length; i++) {
        var quant = product.packages[i].quantity;
        var priceI = product.packages[i].price;
        var pack = {
          quantity: quant,
          quantity_cart: this.findQuantityCart(product, quant),
          price: priceI,
        };
        data.push(pack);
      }
      return data;
    },
    isFavorite(idProd){
      var fav = this.$store.state.user.favori_list;
      return fav.filter(el => el == idProd).length == 1
    },
    findQuantityCart(product, packag) {
      var qt = 0;
      var cart = this.$store.state.cart.data;
      for (var i = 0; i < cart.length; i++) {
        if (cart[i].id === product.id && cart[i].package === packag) {
          qt = cart[i].quantity;
          break;
        }
      }
      return qt;
    },
  },

    mounted() {
        // Fetch the first page of the catalog
        this.products = useObservable(
            liveQuery(async () =>

                fromLocalDatabaseProductEntries(await this.db.products.toArray())
                    .map(product => toLocalizedProduct(product, "fr"))
                    .filter(product =>
                        product.name.toLowerCase().indexOf( this.filters.search.toLowerCase() ) !== -1
                        // product.category.toLowerCase().indexOf( this.filters.category.toLowerCase() ) != -1 &&
                        // product.origin.toLowerCase().indexOf( this.filters.origin.toLowerCase() ) != -1 &&
                        // product.seasonal === this.filters.seasonal &&
                        // product.discount >= this.filters.discount &&
                        // product.favorite === this.filters.favorite
                    )

            )
        )

        this.$store.dispatch("catalog.syncProducts")
    },
};
</script>


<style scoped>
.catalog {
  width: 100%;
  height: 100%;

  overflow-y: scroll;
}

.products {
  display: grid;
  gap: 2vw;
}

.products-phone {
  grid-template-columns: 1fr;
}

.products-tablet {
  grid-template-columns: 1fr 1fr;
}
</style>
